		tom_kkha_s_mitbolami: "  &nbsp;",
		ma_po_tofu_s_farshem: "&nbsp;&nbsp; &nbsp;",
		fo_s_mitbolami: "      ",
		miso_s_fishbolami: "- &nbsp; &nbsp; ",
